import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useHistory, Link } from 'react-router-dom'

import Fundo from '../../assets/images/fundo2.jpg'

import { BodyLogin, FilterImage, ContainerLogin, ContainerContent, ListLogin, Forget, Brand } from '../../styles/styleLogin'
import '../../assets/dist/css/bootstrap.min.css'

import { notification } from "antd";


function Login() {
  const [formData, setFormData] = useState({
    login: '',
    password: '',
  })

  const handleFormDataLogin = (e) => {
    setFormData({ ...formData, login: e.target.value })
  }
  const handleFormDataPassword = (e) => {
    setFormData({ ...formData, password: e.target.value })
  }

  useEffect(() => {
    document.title = "Clnica Pomarola | Login"
  }, []);

  let history = useHistory();

  const handleLogin = async (e) => {
      e.preventDefault();
    // Faz requisio  API
    let wasLogged = null
    axios.post('https://clinica-pomarola-api.herokuapp.com/login', formData).then((res) => {
      // Se bem sucedida, retorna os dados inseridos no res.data e faz o push
      localStorage.setItem('Authorization', `Bearer ${res.data.token}`)
      wasLogged = res.data.id
    }).catch((err) => {
      // Retorna array de erros, abaixo acessa o primeiro
      notification.warning({
        message: "Ateno",
        description: err.response.data[0].msg,
      })
    }).finally(() => {
      if (wasLogged){
        history.push('/cliente');
      }
    })
  };

  return (
    <BodyLogin>
      <FilterImage/>
      <img src={Fundo} alt="fundo do pgina"/>
      <ContainerLogin>
        <ContainerContent>
          <ul>
            <Link to="/">
              <ListLogin active>LOGIN</ListLogin>
            </Link>
            <Link to="/cadastro">
              <ListLogin>CADASTRAR</ListLogin>
            </Link>
          </ul>
          <form>
            <input type="text" value={formData.login} onChange={handleFormDataLogin} maxLength="20" placeholder="Nome de usurio"></input>
            <input type="password" value={formData.password} onChange={handleFormDataPassword} placeholder="Senha"></input>
          </form>
          <Forget>
            <a href="/">ESQUECI MINHA SENHA</a>
            <a href="/">ESQUECI MEU NOME DE USURIO</a>
          </Forget>
          <button onClick={handleLogin}>ENTRAR</button>
        </ContainerContent>
        <ContainerContent>
          <Brand>
            <h1>Clnica Pomarola</h1>
            <p>Cuidando de sua sade,</p>
            <p>onde voc estiver.</p>
          </Brand>
        </ContainerContent>
      </ContainerLogin>
    </BodyLogin>
  );
}

export default Login;